@use './global/library.scss' as lib;

$counter-color: #ffffff;
$counter-background: #E46942;

.additional-panel__list {
	@include lib.reset-list;
	display: grid;
	grid-template-columns: repeat(3, 20px);
	grid-auto-rows: 20px;
	gap: 14px 28px;
	width: 116px;
}

.additional-panel__link {
	display: flex;
	align-items: center;
	justify-content: center;

	position: relative;
}

.additional-panel__counter {
	position: absolute;
	top: -7px;
	right: -14px;
	font-size: 10px;
	line-height: 10px;
	font-weight: 700;
	color: $counter-color;
	background-color: $counter-background;
	padding: 4px 3px 5px 3px;
	border-radius: 50%;
}